<template>
  <div class="login">
    <p class="login-img">
      <img src="../../common/images/dengluimg.png" alt>
    </p>
    <div class="login-content">
      <p class="login-content-item">
        <span class="iconfont icon-wode"></span>
        <input type="text" placeholder="请输入手机号">
        <input type="button" value="获取验证码" class="yzmBtn">
      </p>
      <p class="login-content-item">
        <span class="iconfont icon-wode"></span>
        <input type="text" placeholder="图片验证码">
        <img src="http://localhost:6961/Shared/GetValidateCode?" alt>
      </p>
      <p class="login-content-item">
        <span class="iconfont icon-wode"></span>
        <input type="text" placeholder="请输入验证码">
      </p>
    </div>
    <div class="desc">
      <img src="../../common/images/hehu.png" alt class="desc-img">
    </div>
    <div class="user-agreement">
      <input type="checkbox" class="agree-check" @click="CheckItem()" :checked="flag">
      <span>已阅读并同意</span>
      <span class="agreement" @click="userAgreementBtn()">用户协议</span>
    </div>
    <div class="login-btn" :class="flag? 'yes' : 'no' ">
      <p class="login-btn-content" @click="loginClick()">登录</p>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
        flag:true
    }
  },
  methods: {
    CheckItem (item) {
      this.flag=!this.flag
    },
    loginClick (){
       if(this.flag==true){
           this.$router.push('/home')
       }else{

       }
      
    },
    userAgreementBtn(){
        this.$router.push('agreement')
    }
  }
};
</script>

<style lang="less">
.login {
  width: 100%;
  height: 100vh;
  background: #fff;
  //顶部图片
  .login-img {
    width: 100%;
    height: 408px;
    img {
      width: 100%;
    }
  }
  //登录信息
  .login-content {
    padding: 30px 40px;
    .login-content-item {
      height: 80px;
      display: flex;
      align-items: center;
      border-bottom: 1px solid #f5f5f5;
      .yzmBtn {
        background: #e6706e;
        color: #fff;
        height: 68px;
        border-radius: 8px;
        width: 154px;
      }
      input {
        width: 70%;
        font-size: 26px;
        height: 68px;
      }
    }
  }

  //描述信息
  .desc {
    width: 100%;
    margin-top: 100px;
    .desc-img {
      width: 100%;
    }
  }
  //用户协议
  .user-agreement {
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 200px;
    font-size: 28px;
    .agree-check {
      margin-right: 10px;
    }
    .agreement {
      border-bottom: 1px solid #007aff;
      color: #007aff;
    }
  }

  //底部登录按钮
  .login-btn {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100px;
    line-height: 100px;
    z-index: 22;
    text-align: center;
    color: #fff;
    background: #a8a8a8;

    &.yes {
      background: #e6706e;
    }
    &.no {
      background: #a8a8a8;
    }
  }
}
</style>



